import React from 'react';
import ReactDOM from "react-dom/client";
import './css/index.css'
import './css/design-system.css'
import * as serviceWorker from './serviceWorker';
import { ApolloClient, createHttpLink, InMemoryCache, ApolloProvider, split, gql } from "@apollo/client";
import { setContext } from '@apollo/client/link/context';
import { GraphQLWsLink } from '@apollo/client/link/subscriptions';
import { getMainDefinition } from "@apollo/client/utilities";
import { createClient } from 'graphql-ws';
import App from './App';
import store from 'store2'
import { jwtDecode } from 'jwt-decode'

// Widget initialization function
export function initChatWidget(config = {}) {
  const {
    containerId = 'chat-widget-root',
    publicKey,
    graphqlHttpUrl,
    graphqlWsUrl,
    defaultContractId,
    companyLogoUrl,
    apiUrl,
    ipifyUrl
  } = config;

  // Set environment variables from config
  if (publicKey) window.REACT_APP_PUBLIC_KEY = publicKey;
  if (graphqlHttpUrl) window.REACT_APP_GRAPHQL_HTTP_URL = graphqlHttpUrl;
  if (graphqlWsUrl) window.REACT_APP_GRAPHQL_WS_URL = graphqlWsUrl;
  if (defaultContractId) window.REACT_APP_DEFAULT_CONTRACT_ID = defaultContractId;
  if (companyLogoUrl) window.REACT_APP_COMPANY_LOGO_URL = companyLogoUrl;
  if (apiUrl) window.REACT_APP_API_URL = apiUrl;
  if (ipifyUrl) window.REACT_APP_IPIFY_URL = ipifyUrl;

  const httpLink = createHttpLink({ 
    uri: graphqlHttpUrl || process.env.REACT_APP_GRAPHQL_HTTP_URL 
  });

  const authLink = setContext((_, { headers }) => {
    const token = store("token");
    if (token === null) {
      return headers;
    }
    return {
      headers: {
        ...headers,
        authorization: `Bearer ${token}`
      }
    };
  });

  const wsLink = new GraphQLWsLink(createClient({
    url: graphqlWsUrl || process.env.REACT_APP_GRAPHQL_WS_URL,
    options: {
      reconnect: true,
      connectionParams: () => ({
        authToken: store("token")
      })
    }
  }));

  const link = split(
    ({ query }) => {
      const { kind, operation } = getMainDefinition(query)
      return kind === 'OperationDefinition' && operation === 'subscription'
    },
    wsLink,
    authLink.concat(httpLink),
  )

  const client = new ApolloClient({
    link,
    cache: new InMemoryCache().restore(window.__APOLLO_STATE__),
  })

  const ClientApp = () => (
    <ApolloProvider client={client}>
      <App />
    </ApolloProvider>
  );

  store('contractId', defaultContractId || process.env.REACT_APP_DEFAULT_CONTRACT_ID);

  const CONSUMER_LOGIN = gql`
    mutation consumerLogin($publicKey: UUID!) {
      consumerLogin(input: {
        publicKey: $publicKey
      }) {
        jwtToken
      }
    }
  `;

  const loginClient = new ApolloClient({
    link: httpLink,
    cache: new InMemoryCache(),
  });

  // Find or create container
  let container = document.getElementById(containerId);
  if (!container) {
    container = document.createElement('div');
    container.id = containerId;
    document.body.appendChild(container);
  }

  const root = ReactDOM.createRoot(container);

  const renderApp = (token) => {
    try {
      if (token) {
        try {
          const decodedToken = jwtDecode(token);
          console.log('decodedToken', decodedToken)
          console.log('websiteId', decodedToken.user_id)
          store('websiteId', decodedToken.user_id)
          store("token", token);
        } catch (tokenError) {
          console.error('Failed to decode token:', tokenError)
        }
      } else {
        console.log('No authentication token available')
        const websiteId = store('websiteId')
        if (websiteId) {
          console.log('Using existing websiteId from localStorage:', websiteId)
        } else {
          console.warn('No websiteId available')
          const fallbackContractId = defaultContractId || process.env.REACT_APP_DEFAULT_CONTRACT_ID
          if (fallbackContractId && !store('contractId')) {
            console.log('Using environment fallback contract for backward compatibility')
            store('contractId', fallbackContractId)
          }
        }
      }

      root.render(
        <React.StrictMode>
          <ClientApp />
        </React.StrictMode>
      );

    } catch (error) {
      console.error('Critical error in renderApp:', error)
      const fallbackContractId = defaultContractId || process.env.REACT_APP_DEFAULT_CONTRACT_ID
      if (fallbackContractId) {
        store('contractId', fallbackContractId)
      }

      try {
        root.render(
          <React.StrictMode>
            <ClientApp />
          </React.StrictMode>
        );
      } catch (renderError) {
        console.error('Failed to render app even with fallbacks:', renderError)
      }
    }
  }

  loginClient.mutate({
    mutation: CONSUMER_LOGIN,
    variables: {
      publicKey: publicKey || process.env.REACT_APP_PUBLIC_KEY
    }
  }).then(response => {
    const token = response?.data?.consumerLogin?.jwtToken;
    renderApp(token);
  }).catch(error => {
    console.error("Login mutation failed", error);
    renderApp(null);
  });

  // Return cleanup function
  return () => {
    root.unmount();
    if (container && container.parentNode) {
      container.parentNode.removeChild(container);
    }
  };
}

// Auto-initialize if config is provided via window
if (typeof window !== 'undefined' && window.ChatWidgetConfig) {
  initChatWidget(window.ChatWidgetConfig);
}

// Export for manual initialization
export default { initChatWidget };